
import { defineComponent, PropType } from 'vue';
import { Address } from '../types/user'
import { Company } from '../types/user'

export default defineComponent({
    name:'UserVue',
    props: {
        id:Number,
        name:String,
        username:String,
        email:String,
        address:Object as PropType<Address>,
        phone:String,
        website:String,
        company:Object as PropType<Company>,
        photo:String
    }
})
